	module.exports = {"data":{"post":{"id":"/home/runsisi/ananjingjing/anjing1008.github.io/content/posts/2018-12-18--yoga-asanas-Tadasana/index.md absPath of file >>> MarkdownRemark","html":"<p>感谢贝贝提供插画(蝴蝶)                 </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/蝴蝶-724c6d0fb94ff9fe3bbd27e177eba91e-74164.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 647px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 83.46213292117464%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"unsplash.com\"\n        title=\"\"\n        src=\"/static/蝴蝶-724c6d0fb94ff9fe3bbd27e177eba91e-74164.jpg\"\n        srcset=\"/static/蝴蝶-724c6d0fb94ff9fe3bbd27e177eba91e-e7112.jpg 200w,\n/static/蝴蝶-724c6d0fb94ff9fe3bbd27e177eba91e-89737.jpg 400w,\n/static/蝴蝶-724c6d0fb94ff9fe3bbd27e177eba91e-74164.jpg 647w\"\n        sizes=\"(max-width: 647px) 100vw, 647px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>一、 山式的名称<br>\n英文名: Mountain pose<br>\n梵文名: tadasana or Samasthiti   </p>\n<p>梵文 Tada 是山,<br>\nSama 的意思是不动，<br>\nSthiti 就是站立不动的，<br>\ntadasana 这个体式就是说要像山一样牢固地站立不动，这是一个基本的站立姿势。<br>\n<br>\n双脚并拢，收腹挺胸，腰背挺直，肩膀放松，把身体重量均匀分布于两脚，收紧腿部、臀部的肌肉。<br>\n<br>\n二、练习步骤    </p>\n<ol>\n<li>站立于垫子上，双脚并拢，把身体重心均匀分布于脚跟，找到双脚的“根”。    </li>\n<li>保持双脚的根基力量，感受足弓向上，将力量向上传递。   </li>\n<li>保持大小腿的力量，髌骨向上。   </li>\n<li>保持骨盆的正中位，微收尾骨向下，耻骨向上提。   </li>\n<li>腹部紧致，“核心”稳定。    </li>\n<li>绕肩向后，肩膀放松，肩胛骨往前轻轻推胸部帮助其展开，微收肋骨，不要让胸腔过分挺出。   </li>\n<li>头部向上延伸，锁骨向两旁延展，肩部远离耳朵。   </li>\n<li>感受头顶向上，脚底向下，身体上下相互对应，展开臀部、腹部、腰部、胸部。    </li>\n<li>放松面部表情，视线柔和地平视前方。保存均匀缓慢的呼吸。<br>\n</li>\n</ol>\n<p>三、观察角度<br>\n矢状面: 2、3脚趾中间到膝盖、到髂骨承一条直线<br>\n冠状面: 脚踝、膝盖外侧、髋外侧、肩峰、耳朵、头顶承一条直线    </p>\n<p>四、练习益处<br>\n加强足部活力，增强双脚、腿部以及臀部的力量，改善驼背等不良体态，增强身体的平衡感和协调性。   </p>","fields":{"slug":"/yoga-asanas-Tadasana/","prefix":"2018-12-18"},"frontmatter":{"title":"Yoga asanas1:Mountain pose (Tadasana)","author":"文:Jane 图(蝴蝶.jgp):贝贝","category":"yoga","cover":{"childImageSharp":{"resize":{"src":"/static/sea-bb7ed77233fb16ac505d54f27a0f7aea-ada8c.jpg"}}}}},"authornote":{"id":"/home/runsisi/ananjingjing/anjing1008.github.io/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p>hustlrb<br>\n<br>\n明德厚学 求是创新</p>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/yoga-asanas-Tadasana/","prev":{"id":"/home/runsisi/ananjingjing/anjing1008.github.io/content/posts/2018-12-11--excessive-interoperability/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/excessive-interoperability/","prefix":"2018-12-11"},"frontmatter":{"title":"过度协助","category":"thought"}},"next":{"id":"/home/runsisi/ananjingjing/anjing1008.github.io/content/posts/2018-12-22--the-7-Habits-of-Highly-Effective-People/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/the-7-Habits-of-Highly-Effective-People/","prefix":"2018-12-22"},"frontmatter":{"title":"高效能人士的七个习惯","category":"reading notes"}}}}

